@media ( min-width : 320px ) {

  header {
    align-items: center;
    display: flex;
    flex-direction: column;
    font-family: Bebas Neue, cursive;
    justify-content: space-between;
    letter-spacing: 2px;
    margin: auto;
    text-align: center;
  }

  .img2Logo {
    margin: 20px 0;
    width: 200px;
  }

  .iconCoin {
    visibility: hidden;
  }

  .moneyHeader {
    align-items: center;
    background-color: rgb(218, 211, 211);
    border-radius: 10px;
    box-shadow: 0 2px 10px 2px rgb(57, 57, 57);
    display: flex;
    justify-content: space-around;
    margin-top: 2px;
    max-height: 50px;
    max-width: 90%;
    padding: 10px 0;
    width: 90%;
  }

  .totalHeader {
    font-size: 16px;
    margin: 0 10px;
    max-width: 140px;
    text-align: center;
    width: 70px;
  }

  .valorHeader , .totalAtt {
    color: rgb(6, 6, 6);
    font-size: 16px;
    font-weight: 900;
    height: 24px;
    margin: 0 5px;
    margin-top: 5px;
    max-width: 350px;
    overflow: hidden;
    text-align: left;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 350px;
  }

  .brlHeader {
    font-size: 16px;
    margin: 0 25px 0 5px;
  }

  .iconProfile {
    background-color: rgb(67, 135, 135);
    visibility: hidden;
  }

  .emailHeader {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 12px;
    font-weight: 900;
    max-width: 250px;
    overflow: hidden;
    text-align: center;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 250px;
  }
}

